.todo-list {
  background-color: white;
  box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.15);
  padding: 15px 25px;
  margin: 0 15px;
  margin-bottom: 50px;
}

.todo-item {
  width: 100%;
  border-bottom: 1px solid #cecece;
  font-family: 'Roboto';
  font-weight: 300;
  font-size: 20px;
  color: #333333;
  padding: 20px 0;
}

.todo-item-left {
  float: left;
}

.todo-item-left span {
  word-break: break-all;
}

.removeTask {
  float: right;
  color: #000;
  cursor: pointer;
  border-radius: 4px;
  width: 28px;
  height: 28px;
  text-align: center;
  font-weight: 500;
  font-size: 20px;
}

.removeTask:hover {
  background-color: #ff1d00;
  color: #fff;
}

input[type='checkbox'] {
  margin-right: 10px;
  width: 20px;
  height: 20px;
  vertical-align: middle;
}

.completedTask {
  font-size: 18px;
  text-decoration: line-through #5a5a5a;
  transition: 0.5s all;
}

.no_data {
  width: 100%;
  border-bottom: 1px solid #cecece;
  font-family: 'Roboto';
  font-weight: 300;
  font-size: 30px;
  color: #444444;
  padding: 10px;
}

@media screen and (min-width: 1170px) {
  .todo-list {
    padding: 30px 50px;
  }

  input[type='checkbox'] {
    width: 22px;
    height: 22px;
  }
}

@media screen and (min-width: 600px) and (max-width: 1170px) {
  .todo-list {
    padding: 30px 50px;
  }

  input[type='checkbox'] {
    width: 22px;
    height: 22px;
  }
}
