.dashboard {
  padding: 20px;
}
.dashboard-item {
  background-color: #fff;
  margin: 10px 0;
  padding: 15px;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  cursor: pointer;
}

.dashboard-item-title {
  font-size: 18px;
  padding-bottom: 5px;
}

.dashboard-item-list p {
  padding: 4px;
}
